const SERVER_API_BASE =
  process.env.API_BASE_URL || process.env.API_PROXY_TARGET || process.env.NEXT_PUBLIC_API_BASE || "http://localhost:4000/api";
const BROWSER_API_BASE = process.env.NEXT_PUBLIC_API_BASE || "/api";

export function getServerApiBase() {
  return SERVER_API_BASE;
}

export function getBrowserApiBase() {
  if (typeof window === "undefined") {
    return SERVER_API_BASE;
  }
  return BROWSER_API_BASE || SERVER_API_BASE;
}
