"use client";
import Link from "next/link";
import { usePathname, useRouter } from "next/navigation";
import { ReactNode, useEffect, useMemo, useState } from "react";
import ThemeToggle from "../ThemeToggle";
import { getBrowserApiBase } from "@/lib/api";
import { AnimatePresence, motion } from "framer-motion";
import { CloseIcon, LogoutIcon, MenuIcon } from "../Icon";

type Me = {
  id: string;
  name: string;
  role: "admin" | "accountant" | "user";
};

const navItems = [
  { label: "Snapshot", href: "/" },
  { label: "People", href: "/people", roles: ["admin"] },
  { label: "Deposits", href: "/deposit", roles: ["admin", "accountant"] },
  { label: "Withdraw & Invest", href: "/withdraw", roles: ["admin", "accountant"] },
  { label: "Exports", href: "/export", roles: ["admin", "accountant"] },
  { label: "Yearly collection", href: "/yearly", roles: ["admin", "accountant"] },
  { label: "Balances", href: "/balances", roles: ["admin", "accountant"] },
  { label: "Setup", href: "/setup" },
];

export default function AppShell({ me, children }: { me: Me; children: ReactNode }) {
  const pathname = usePathname();
  const router = useRouter();
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  useEffect(() => {
    if (!mobileMenuOpen) {
      document.body.style.overflow = "";
      return;
    }
    const prev = document.body.style.overflow;
    document.body.style.overflow = "hidden";
    return () => {
      document.body.style.overflow = prev;
    };
  }, [mobileMenuOpen]);

  const links = useMemo(() => navItems.filter((item) => !item.roles || item.roles.includes(me.role)), [me.role]);

  const handleLogout = () => {
    fetch(`${getBrowserApiBase()}/auth/logout`, { method: "POST", credentials: "include" })
      .catch(() => undefined)
      .finally(() => router.push("/login"));
  };

  return (
    <div className="min-h-screen flex text-slate-900 dark:text-slate-100 bg-gradient-to-br from-[#FDF5FF] via-[#FEE0F0] to-[#EEE9FF] dark:from-[#09031a] dark:via-[#120825] dark:to-[#080516]">
      <aside className="hidden lg:flex w-72 flex-col border-r border-white/40 dark:border-slate-800 bg-white/70 dark:bg-slate-900/80 backdrop-blur-xl">
        <div className="px-6 pt-8 pb-4">
          <div className="text-xs uppercase tracking-[0.2em] text-slate-400">Community savings</div>
          <div className="text-xl font-semibold mt-1 text-slate-900 dark:text-white">Dashboard</div>
          <p className="text-sm text-slate-500 dark:text-slate-400 mt-2">
            Track group money in plain language. Everything important sits one click away.
          </p>
        </div>
        <nav className="flex-1 px-4 space-y-1 overflow-y-auto pb-4">
          {links.map((item) => {
            const active = pathname === item.href;
            return (
              <Link
                key={item.href}
                href={item.href}
                className={`flex flex-col rounded-xl px-4 py-3 border transition-all ${
                  active
                    ? "border-blue-200 bg-blue-50 text-blue-700 dark:border-blue-500/40 dark:bg-blue-500/10 dark:text-blue-100"
                    : "border-transparent hover:border-blue-100 hover:bg-blue-50/60 dark:hover:border-slate-700 dark:hover:bg-slate-800/50"
                }`}
              >
                <span className="font-medium">{item.label}</span>
              </Link>
            );
          })}
        </nav>
        <div className="px-4 pb-6 space-y-3">
          <div className="rounded-2xl border border-slate-100 dark:border-slate-700 p-4 bg-white/70 dark:bg-slate-900/60">
            <p className="text-xs uppercase tracking-wider text-slate-400">Signed in as</p>
            <p className="text-sm font-semibold mt-1 text-slate-900 dark:text-white">{me.name || "Member"}</p>
            <p className="text-xs text-slate-500 dark:text-slate-400 capitalize">{me.role}</p>
            <button onClick={handleLogout} className="mt-4 inline-flex items-center gap-2 text-sm font-medium text-rose-600 hover:text-rose-500 dark:text-rose-400">
              Logout
            </button>
          </div>
          <ThemeToggle />
        </div>
      </aside>

      <div className="flex-1 flex flex-col">
        <div className="lg:hidden border-b border-slate-100 dark:border-slate-800 bg-white/90 dark:bg-slate-900/80 backdrop-blur px-4 py-3 flex items-center justify-between">
          <div>
            <div>
              <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Today</p>
              <p className="text-lg font-semibold text-slate-900 dark:text-white">Community savings</p>
            </div>
          </div>
          <button
            type="button"
            onClick={() => setMobileMenuOpen(true)}
            className="inline-flex items-center gap-2 rounded-full border border-slate-200 dark:border-slate-700 px-3 py-2 text-sm font-medium"
          >
            Menu
            <MenuIcon className="w-5 h-5" />
          </button>
        </div>

        <main className="flex-1 px-4 py-6 sm:px-6 lg:px-10 lg:py-8 bg-transparent">
          <div className="mx-auto max-w-6xl">{children}</div>
        </main>
      </div>

      <AnimatePresence>
        {mobileMenuOpen ? (
          <motion.div
            className="fixed inset-0 z-50 bg-black/40 backdrop-blur-sm lg:hidden"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => setMobileMenuOpen(false)}
          >
            <motion.div
              className="ml-auto h-full w-full max-w-xs bg-white dark:bg-slate-900 shadow-2xl border-l border-slate-100 dark:border-slate-800 flex flex-col"
              initial={{ x: "100%" }}
              animate={{ x: 0 }}
              exit={{ x: "100%" }}
              transition={{ duration: 0.2 }}
              onClick={(e) => e.stopPropagation()}
            >
              <div className="flex items-center justify-between px-5 py-4 border-b border-slate-100 dark:border-slate-800">
                <div>
                  <p className="text-xs uppercase tracking-[0.3em] text-slate-400">Signed in</p>
                  <p className="text-base font-semibold text-slate-900 dark:text-white">{me.name || "Member"}</p>
                </div>
                <button type="button" onClick={() => setMobileMenuOpen(false)} className="p-2 rounded-full hover:bg-slate-100 dark:hover:bg-slate-800" aria-label="Close menu">
                  <CloseIcon />
                </button>
              </div>
              <nav className="flex-1 overflow-y-auto px-4 py-4 space-y-2">
                {links.map((item) => {
                  const active = pathname === item.href;
                  return (
                    <Link
                      key={item.href}
                      href={item.href}
                      onClick={() => setMobileMenuOpen(false)}
                      className={`block rounded-2xl px-4 py-3 border ${
                        active
                          ? "border-blue-300 bg-blue-50 text-blue-700 dark:border-blue-500/40 dark:bg-blue-500/10 dark:text-blue-100"
                          : "border-slate-200 text-slate-600 dark:border-slate-700 dark:text-slate-200"
                      }`}
                    >
                      {item.label}
                    </Link>
                  );
                })}
              </nav>
              <div className="border-t border-slate-100 dark:border-slate-800 p-4 space-y-3">
                <button
                  type="button"
                  onClick={handleLogout}
                  className="w-full inline-flex items-center justify-center gap-2 rounded-2xl border border-rose-200 text-rose-600 py-2 font-medium"
                >
                  <LogoutIcon className="w-4 h-4" />
                  Logout
                </button>
                <div className="flex justify-center">
                  <ThemeToggle />
                </div>
              </div>
            </motion.div>
          </motion.div>
        ) : null}
      </AnimatePresence>
    </div>
  );
}
